@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@100&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Ubuntu:wght@300&display=swap);
:root{
  --clr-btn: rgb(250, 92, 0);
  --default-font-family: 'Roboto', sans-serif;
  --default-transition-duration: 500ms;
  --header-fade-duration: 2s ease-in-out;
  --default-background-color: rgb(0, 0, 0);
  --bg-color-effect: rgb(43, 41, 41);
  --bg-color-faded: rgb(32, 32, 32);
  --title-lg: 2rem;
}

*{
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

body, html {
  /* font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif; */
  font-family: 'Roboto', sans-serif;
  font-family: var(--default-font-family);
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  width: 100vw;
  height: 100vh;
  line-height: 1.5rem;
  scroll-behavior: smooth;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

#root{
  height: 100%;
  background-color: aqua;
}


.app{
  height: 100%;
}

.section{
  height: 100%;
}

.section-bg:nth-child(even){
  background-color: var(--default-background-color);
  color: white;
}

/*custom*/
.flex-center{
  display: flex;
  justify-content: center;
  align-items: center;
}

.btn-fw{
  font-weight: 500;
  background-color: var(--clr-btn);
  color: black;
  border-radius: 10px;
  border: 2px solid var(--clr-btn);
  min-width: 12ch;
  padding: 0.2em 0.5em;
  text-align: center;
  transition-duration: var(--default-transition-duration);
  cursor: pointer;
}

.btn-fw:hover{
  background-color: var(--bg-color-effect);
  color: white;
}

.btn{
  display: inline;
  font-weight: bold;
  color: var(--clr-btn);
  transition-duration: var(--default-transition-duration);
  cursor: pointer;
}

.btn:hover{
  background-color: var(--bg-color-effect);
  color: white;
}

footer{
  background-color: black;
  color: white;
  text-align: center;
  padding: 1em;
}

.footer-nav{
  display: flex;
  justify-content: center;
  color: var(--clr-btn);
  padding: 0 0.2em;
  cursor: pointer;
}

.footer-nav-item{
  white-space: nowrap;
  font-size: clamp( 0.7rem, 2.5vw, 1rem );
}

.footer-nav > div:hover{
  opacity: 0.5;
  transition: opacity var(--default-transition-duration);
}

.footer p a{
  color: white;
}
.header-active{
  position: fixed;
  top: 0;
  width: 100%;
  background-color: rgba(102, 51, 153, 0);
  color: white;
  padding: 1em 3em;
  opacity: 1;
  transition-duration: 1s;
}


.logo{
  display:inline;
  text-shadow: 2px 2px red;
  border-radius: 50px;
  border: 2px solid var(--clr-btn);
  padding: 0.2em 0.4em;
  
}

header nav{
  display: flex;
  justify-content: flex-start;
  margin-top: 1em;
}

header nav ul{
  display: flex;
  justify-content: center;
  grid-gap: 0.6em;
  gap: 0.6em;
  list-style: none;
  width: 100%;
}
header ul li{
  position: relative;
  cursor: pointer;
  padding: 3px 0;
}

header ul li::before{
  content: '';
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  border-bottom: 2px solid var(--clr-btn);
  -webkit-transform: scale(0);
          transform: scale(0);
  transition-duration: var(--default-transition-duration);
}

header ul li:hover::before{
  -webkit-transform: scale(1);
          transform: scale(1);
}

header ul li a {
  font-size: clamp( 0.7em, 2.5vw, 1.2em);
}


/* custome */
.fade-out{
  opacity: 0;
  -webkit-transform: translateY(-100%);
          transform: translateY(-100%);
}


/* media query */
@media screen and (min-width: 900px) {
  
  header nav ul{
    justify-content: end;
  }

}
.main{
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  background-color: black;
  color: white;
  padding: 2em;
  background-image: url(/norvillie/static/media/norville.057b94e1.webp);
  background-repeat: no-repeat;
  background-position:right;
  background-size:cover;
  min-height: 100%;
  overflow-x: hidden;
}

.main .left-side{
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  flex-grow: 2;
}

.left-side .icons{
  display: flex;
  justify-content: center;
  margin: 0.5em 0;
  grid-gap: 2em;
  gap: 2em;
}

.left-side .icons a{
  color: white;
}

.main .title{
  text-align: center;
}

.main .left-side .btn-cta{
  display: flex;
  justify-content: center;
  padding: 0.1em;
  grid-gap: 0.3em;
  gap: 0.3em;
}

.main .icons .icons-item{
  cursor: pointer;
  font-size: 1.3em;
  transition-duration: var(--default-transition-duration);
}

.main .icons .icons-item:hover{
  opacity: 0.5;
}

/* right side */
.main .right-side{
  flex-grow: 1;
}



@media screen and (min-width: 900px) {
  
  .main .title{
    font-size: 3em;
    font-family: 'Ubuntu', sans-serif;
    margin: 0.3em 0;
  }

}
.about{
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 0.5em;
  width: 100%;
  height: 100%;
  grid-gap: 1em;
  gap: 1em;
}



.about article{
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width:100%;
  min-height: 100%;
  padding-top: 0.2em;
}

.about article p{
  padding-top: 0.5em;
  font-size: clamp(12px, 2.5vw, 20px);
}

.about article .about-img{
  display: flex;
  width: 250px;
  height: 200px;
}

.about .about-img img{
  width: 100%;
  height: 100%;
  border-radius: 5px;
  box-shadow: 5px 5px 10px;
  object-fit: cover;
}

.about article .about-text{
  display: flex;
  flex-direction: column;
  width: 100%;
  height: 100%;
  padding: 1em 2em;
}

.about .about-text .button{
  width: 25ch;
  align-self: center;
}

.about .about-text span{
  color: var(--clr-btn)
}

.about .about-text a{
  color: black;
  text-decoration: none;
}

.about .about-text a:hover{
  color: white;
}

.about article .btn{
  margin-top: 0.2em;
}

@media screen and (min-width: 1000px) {
  .about article{
    flex-direction: row;
    padding: 0 5em 0 2em;
  }

  .about article .about-img{
    width: 50%;
    height: 50%;
  }

  .about article .about-text{
    margin-top: 0;
    justify-content: center;
  }
}



.contact{
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  width: 100%;
  padding: 2em;
}


.contact h2{
  text-align: center;
  margin-bottom: 1em;
  font-size: 2em;
}

.contact .contact-form{
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  width: 100%;
  grid-gap: 1em;
  gap: 1em;
}

.contact .contact-form > div{
  margin-top: 0.2;
  width: 100%;
}

.contact .contact-form div:nth-of-type(1){
 display: flex;
 flex-wrap: wrap;
 grid-gap: 1em;
 gap: 1em;
}

.contact .contact-form div:nth-of-type(1) > input{
  width: 100%;
}

.contact .contact-form input{
  width: 100%;
  border: none;
  padding: 1em;
  border-bottom: 1px solid rgb(128, 128, 128);
}

.contact .contact-form textarea{
  border: none;
  border-bottom: 1px solid rgb(128, 128, 128);
  width: 100%;
  -webkit-columns: 2;
          columns: 2;
  padding: 1em;
}



@media screen and (min-width: 900px) {
  .contact .contact-form div:nth-of-type(1){
    flex-wrap: nowrap;
   }

   .contact{
     padding: 15em;
   }

   .contact * {
     font-size: x-large;
   }

   .contact h2{
     font-size: 3em;
   }
}


.greetings{
  position: absolute;
  flex-direction: column;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: black;
  color: white;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
}
.works {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 3em 1.5em;
}

.works .title{
  font-size: var(--title-lg);
}

.works .works-wrapper{
  width: 100%;
  height: 80%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding: 2em 0;
  grid-gap: 1em;
  gap: 1em;
}


.works-wrapper .work-item{
  background-color: var(--bg-color-faded);
  padding: 1em;
  width: 100%;
  height: 100%;
}

.work-item.related{
  position: relative;
  height: 100%;
}

.work-item.non-related {
  position: relative;
  height: 100%;
}


.work-item.related .item-list{
  height: 100%;
}


.works-wrapper .item-list .item{
  position: absolute;
  top:50%;
  left: 0;
  -webkit-transform: translatey(-60%);
          transform: translatey(-60%);
  display: flex;
  flex-direction: column;
  align-items: center;
  font-size: large;
  width: 100%;
  opacity: 0;
  grid-gap: 0.2em;
  gap: 0.2em;
  transition-duration: var(--default-transition-duration);
}

.works-wrapper .item-list .item h3{
  font-size: smaller;
}

.works-wrapper .item-list .item p{
  font-size: smaller;
}

.works-wrapper .item-list .item a{
  color: rgb(170, 170, 236);
  font-size: smaller;
}

.works-wrapper .item-list .item.active{
  opacity: 1;
  padding: 0.5em 1em;
  z-index: 10;
}

.works-wrapper .work-item .btn-nav{
  position:absolute;
  left: 0;
  bottom: 10%;
  width: 100%;
  display: flex;
  justify-content: center;
  grid-gap: 1em;
  gap: 1em;
  cursor: pointer;
}


.works-wrapper .work-item .btn-nav .btn-nav-dot{
  display: flex;
  grid-gap: 1em;
  gap: 1em;
}

.works-wrapper .work-item .btn-nav .btn-nav-dot .dot-active{
  color: red;
  border-bottom: 3px solid;
}



@media screen and (min-width: 900px) {
  .works .works-wrapper{
    flex-direction: row;
  }

  .works-wrapper .item-list .item{
    -webkit-transform: translateY(-100%);
            transform: translateY(-100%);
  }
}
.education{
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  padding: 1em;
}

.education-wrapper{
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  width: 100%;
  height: 100%;
  padding: 2em 2em;
  grid-gap: 2em;
  gap: 2em;
  overflow-y: scroll;
  scrollbar-width: none;
}

.education-wrapper .degree{
  background-color: var(--bg-color-faded);
  padding: 0.5em 0.5em;
  border-radius: 5px;
  transition: var(--default-transition-duration);
  width: 100%;
}

.education-wrapper .degree:hover{
  -webkit-transform: scale(1.01);
          transform: scale(1.01);
}

.education-wrapper .degree h5{
  display: inline-block;
  color: var(--clr-btn);
  border: 1px solid var(--clr-btn);
  border-radius: 5px;
  padding: 0.1em 1em;
  margin-top: 0.5em;
}

.education-wrapper .degree > *{
  padding-top: 0.5em;
}

/* media query */
/* is screen is greater than */
@media screen and (min-width: 900px){
  
  .education{
    padding: 3em 0;
  }
  .education .title{
    font-size: 3rem;
  }

  .education-wrapper{
    justify-content: center;
    flex-wrap: nowrap;
    width: 100%;
    height: 100%;
  }

  .education-wrapper .degree{
    max-width: 450px;
    min-height: 200px;
  }
  
  .education-wrapper .degree h3{
    font-size: medium;
  }


}
.skill{
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 3em 2em;
  height: 100%;
}

.skill .skill-wrapper{
  width: 100%;
  height: 100%;  
  padding-top: 0.5em;
  overflow-y: scroll;
  scrollbar-width: none;
}

.skill-wrapper .title{
  text-align: center;
  font-size: 1.5em;
}

.skill-wrapper .title h3{
  font-size: 1em;
}


.skill-wrapper .skills-item{
  margin-top: 0.1em;
}

.skill-wrapper .skills-item .p-bar{
  background-color: orange;
  padding: 0 1em;
  text-align: end;
  border-radius: 5px;
  height: 1.3em;
}

.skill-wrapper .skills-item .p-bar p{
  font-size: 10px;
}

@media screen and  (min-width: 900px ){
  .skill .skill-wrapper{
    width: 80%;
  }
}

/* animate */
.p-start{
  width: 0;
  transition: width 3s ease-in;
}

@media screen and (min-width: 900px) {
  .skill-wrapper .title{
    font-size: var(--title-lg);
  }
}
.achievement{
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 0.5em 3em;
  width: 100%;
  grid-gap: 2em;
  gap: 2em;
  overflow: auto;
}


.achievement p{
  text-align: justify;
  line-height: 1.7em;
}

.achievement .award{
  display: flex;
  flex-direction: row;
  margin-top: 0.2em;
}

.achievement .award .image{
  width: 20%;
  height: 250px;
  background-color: blue;
}

.achievement .award .image img{
  width: 100%;
  height: 100%;
}


.achievement .text{
  width: 80%;
  padding: 1em;
}

p{
  margin-top: 1em;
}

@media screen and (max-width: 700px) {
  .achievement{
    padding: 0.5em;
  }
  
  .achievement .award .image{
    display: none;
  } 
}

